import styles from './styles.scss';

import { videoAnarchyFrame } from '../textures';

export const videoFrame = () => {
  // const manager = 'test';
  const root = document.querySelector('#app');
  const container = document.createElement("div");
  container.id = "videoFrame";
  container.classList.add(styles.root);
  // const btnContent = document.createTextNode("Center");
  // btn.appendChild(btnContent);
  const video = document.createElement("video");
  video.classList.add(styles.video);
  video.src = videoAnarchyFrame;
  video.loop = true;
  video.controls = false;
  video.autoplay = true;
  container.appendChild(video);

  root.appendChild(container);
  return container;
  // return manager;
};
