// textureFbxLoadingManager
import {
  Mesh,
  TextureLoader
} from 'three';

// import { initLoadingManager } from '../loader';
// sceneLoadingIndicato
import { sceneLoadingIndicator, initLoadingManager } from '../loader';

// TextureLoader global fn
const manager = initLoadingManager();
const textureLoader = new TextureLoader( manager );

export const textureFbxLoadingManager = (newTexture, model) => {
  // sceneLoadingIndicator(true);
  textureLoader.load(newTexture, function (texture) {
    model.traverse(function (child) {
      if (child instanceof Mesh) {
        child.material.map = texture;
        // child.material.lightMap = texture;
        child.material.flatShading = true;
        child.material.needsUpdate = true;
      }
    });
  });
  // sceneLoadingIndicator(false);
}

// Texture Sample Loading
export const textureSampleLoadingManager = (texture) => {
  return textureLoader.load(texture);
}

// Change Texture
export const changeModelTexture = (scene, name, texture) => {
  const Model = scene.getObjectByName(name);
  // sceneLoadingIndicator(true);
  if (Model) {
    const parentModel = Model.parent;
    textureFbxLoadingManager(texture, Model);
    // sceneLoadingIndicator(false);
    // parentModel.remove( Model );
  }
}
