// textureFbxLoadingManager
import {
  MeshPhongMaterial,
  SphereGeometry,
  BackSide,
  Mesh,
  CubeGeometry,
  MeshBasicMaterial,
  DoubleSide,
  MeshFaceMaterial
} from 'three';

import { textureSampleLoadingManager } from './textureLoadingManager';

import { textureBackgroundScene } from '../textures';

export const cubeGeometry = (scene) => {

  const geometryCube = new CubeGeometry(2,3,4);

  const materials = [
      new MeshBasicMaterial({color:0x33AA55, transparent:true, opacity:0, side: DoubleSide}),
      new MeshBasicMaterial({color:0x55CC00, transparent:true, opacity:0, side: DoubleSide}),
      new MeshBasicMaterial({color:0x000000, transparent:true, opacity:0, side: DoubleSide}),
      new MeshBasicMaterial({color:0xFF0000, transparent:true, opacity:0, side: DoubleSide}),
      new MeshBasicMaterial({color:0xFF0000, transparent:true, opacity:0, side: DoubleSide}),
      new MeshBasicMaterial({color:0x5555AA, transparent:true, opacity:0, side: DoubleSide}),
  ];

  // Create a MeshFaceMaterial, which allows the cube to have different materials on each face
  const cubeMaterial = new MeshFaceMaterial(materials);

  const cube = new Mesh(geometryCube, cubeMaterial);
  cube.name = 'mainAction';
  cube.position.y = 0.4;
  cube.cursor = 'pointer';
  scene.add( cube );
  cube.cursor = 'pointer';
}
