// Loaders Model
import * as FBXLoader from 'three-fbxloader-offical';
import { textureFbxLoadingManager, changeModelTexture } from './textureLoadingManager';
import { modelPositionY } from './staticSettings';
import {
  textureBackgroundScene,
  headTexture,
  headTextureAlt,
  headTextureGreenHair,
  headTextureBlueHair,
  headTextureVampire,
  headTextureRedHair,
  chainTexture,
  chainTextureVampire,
  hornsTexture,
  fangsTextureVampire,
  headTextureGreen,
  hatTexture,
  hatTextureHead,
  chainTextureHat,
  demonHeadTexture,
  chainTextureDemon,
  chainTextureGreen,
  glassesTexture,
  headTextureAnarchy,
  chainTextureAnarchy,
  glassesTextureTowel
} from '../textures';
import { namespace } from './namespace';
import { removeModel } from './removeModel'
import { initLoadingManager } from '../loader';

const assetsPath = process.env.ASSETS_PATCH;
const manager = initLoadingManager();
const loader = new FBXLoader( manager );
loader.crossOrigin = 'use-credentials';
// loader.setCrossOrigin('anonymous');
// allow cross origin loading
// loader.crossOrigin = '';

const currentLoaded = (scene, name) => scene.getObjectByName(name) ? false : true;

// Horns
export const addModelHorns = (scene) => {
  // const loaded = scene.getObjectByName(namespace.horn) ? false : true;
  loader.crossOrigin = 'use-credentials';
  if (currentLoaded(scene, namespace.horn)) {
    loader.load(assetsPath + "demon/horns.fbx", model => {
      model.name = 'Horns';
      model.visible = false;
      // hornsTexture
      textureFbxLoadingManager(hornsTexture, model);
      scene.add(model);
      model.position.y = modelPositionY; // x,y,z
      // model.rotation.x = -0.1;
      model.updateMatrix();
      // async function asyncCall() {
      //  const result = await scene.getObjectByName(namespace.horn);
      //  console.log(result);
      //  result.visible = true;
      // }
      // asyncCall();
    });
  }
}

