// Head - Main model
export const addModelMain = (scene) => {
  loader.crossOrigin = 'use-credentials';
  loader.load(assetsPath + "default/head.fbx", model => {
    model.name = 'Main';
    model.visible = false;
    // model.renderOrder: 0
    textureFbxLoadingManager(headTexture, model);

    scene.add(model);
    model.position.y = modelPositionY;
    model.updateMatrix();
    // async function asyncCall() {
    //  const result = await scene.getObjectByName(namespace.main);
    //  result.visible = true;
    // }
    // asyncCall();
  });
}

const visibledModels = ({scene, option}) => {

  scene.children.map((item, i) => {
    if (item.type === 'Group') {
      item.visible = false;
    }
  });

  setTimeout( function() {
    scene.children.map((item, i) => {
      // item.visible = false;
      if (item.name === namespace.main) {
        // scene.children[i].visible = true;
        item.visible = option.mainModel || false;
      } else if (item.name === namespace.horn) {
        item.visible = option.hornModel || false;
      } else if (item.name === namespace.chain) {
        item.visible = option.chainModel || false;
      } else if (item.name === namespace.towel) {
        item.visible = option.towelModel || false;
      } else if (item.name === namespace.hat) {
        item.visible = option.hatModel || false;
      } else if (item.name === namespace.fangs) {
        item.visible = option.fangsModel || false;
      } else if (item.name === namespace.glasses) {
        item.visible = option.glassesModel || false;
      }
    });
  }, 1600 );
}

export const addModelMainDemon = (scene) => {
  // removeModel(scene, namespace.fangs);
  // addModelChain(scene);
  changeModelTexture(scene, namespace.chain, chainTextureDemon);
  changeModelTexture(scene, namespace.main, demonHeadTexture);
  // const MainModel = scene.getObjectByName(namespace.main);

  visibledModels({
    scene,
    option: {
      hornModel: true,
      chainModel: true,
      mainModel: true,
    }
  })

}

