// textureFbxLoadingManager
import {
  MeshPhongMaterial,
  SphereGeometry,
  BackSide,
  Mesh,
} from 'three';

import { textureSampleLoadingManager } from './textureLoadingManager';

import { textureBackgroundScene } from '../textures';

export const panoramaSphereGeometry = (scene) => {

  const geometryTexture = textureSampleLoadingManager(textureBackgroundScene);

  var material = new MeshPhongMaterial({
    // color: "#ffffff",
    // shininess: 10,
    map: geometryTexture,
    specular: "#000000",
    side: BackSide,
    name: 'panoramaSphereGeometryMaterial'
  });

  // Add the sphere container.
  // var geometry = new SphereGeometry( 300, 128, 128 );
  var geometry = new SphereGeometry( 10, 128, 128 );
  material.side = BackSide;
  var sphere = new Mesh( geometry, material );
  // sphere.position.set( 0, 0, 0 );
  // sphere.rotation.y = 0.1;
  // sphere.rotation.x = -0.4;
  // sphere.rotation.y = Math.PI/1.4;
  sphere.rotation.y = 2.2;
  sphere.rotation.x = -0.2;
  // sphere.rotation.x = -Math.PI/1.2;
  sphere.side = BackSide;
  sphere.name = 'panoramaSphereGeometry';
  scene.add( sphere );
}
